import React from 'react';
import {withRouter, Redirect} from 'react-router-dom';
import {useState, useEffect} from 'react';

function Updatedata(props){    
                

    //console.warn("props:",props.match.params.id);

    
    const [item, setItem] = useState([])

    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [phone, setPhone] = useState("");
    const [id, setId] = useState(null);

    useEffect(() => {
        fetch("http://localhost/projects/php/restapi/api1/api.php/"+props.match.params.id)
            .then((result) => {
                result.json()
                    .then((resp) => {
                        //console.warn("result", resp)
                        setItem(resp);

                        setName(resp[0].name);
                        setEmail(resp[0].email);
                        setPhone(resp[0].phone);
                        setId(resp[0].id);

                    })
            })
    }, [])

    
    function updateUserdata(){ 
         //console.warn([name, email, phone , id]);
         let data = {name, email, phone , id}
 
         fetch("http://localhost/projects/php/restapi/api1/update.php/" + id, {
             method:"PUT",
             headers:{
                 'Accept':'application/json',
                 'Content-type':'application/json'
             },
             body:JSON.stringify(data)
         }).then((result) => {

            result.json().then((resp)=>{
                console.warn("resp",resp.data);
                if(resp.data){
                    //this.setState({redirect: true});
                    //return (<Redirect to={'/sss'} />)
                    
                }
                 
            })
            
         })        
     }




    return(
        <div><br /><br /><br /><br /><br /><br />
            <input type="text" value={name} onChange={(e)=>{setName(e.target.value)}} name="name" placeholder="Name" /> <br /><br />
                <input type="email" value={email} onChange={(e)=>{setEmail(e.target.value)}} name="email" placeholder="Email" /> <br /><br />
                <input type="phone" value={phone} onChange={(e)=>{setPhone(e.target.value)}} name="phone" placeholder="Phone" /> <br /><br />
                <button type="button" onClick={updateUserdata}>Update User</button>
        </div>
    )
}
export default withRouter(Updatedata);