
export function Pocessdata(type, userData){

    
    let Baseurl = 'http://localhost/projects/php/restapi/api1/';
    //let Baseurl = 'https://api.thewallscript.com/restful/';

    return new Promise((resolves, reject) => {
        //fetch('http://localhost/projects/php/restapi/api1/login.php',{
        fetch(Baseurl+type,{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(userData)
        })
        .then((response)=> response.json())
        .then((responseJson)=>{
            resolves(responseJson);
            //console.resolves(responseJson);
        })
        .catch((error)=>{
            reject(error);
            //console.reject(error);
        });
    })
}
