import React, { Component } from 'react'
import {Redirect, Link} from 'react-router-dom';

import {Pocessdata} from './Pocessdata'
//import Getdata from './Getdata'



class Login extends Component {
    constructor(props){
        super(props);
        this.state = {
            username:'',
            password:'',
            redirect: false
        }
        this.login = this.login.bind(this);
        this.onChange = this.onChange.bind(this);
    }

    login(){
        if(this.state.username && this.state.password){
            Pocessdata('login.php', this.state).then((result) =>{
                let responseJson = result;
                if(responseJson.userdata){
                    sessionStorage.setItem("userdataname", this.state.username);
                    sessionStorage.setItem("userdata", responseJson);
                    localStorage.setItem('userdata', responseJson);
                    localStorage.setItem("userdataname", this.state.username);
                    this.setState({redirect: true});
                }else{
                    console.log("error");
                }
            })
        }else{
            console.warn("username && password Require");
        }         
    }

    onChange(e){
        
        //this.setState({username: e.target.value})
        //this.setState({password: e.target.value})
        this.setState({[e.target.name] : e.target.value});
        //console.warn(this.setState({[e.target.name] : e.target.value}));
        
    }

    
    render() {

        if(this.state.redirect){
            return (<Redirect to={'/'} />)
        }

        if(sessionStorage.getItem("userdata")){
            return (<Redirect to={'/home'} />)
        }

        return (
            <div className="row small-up-2 medium-up-3 large-up-4">
                <div className="column bodyPart">
                    
                    <h1>This is Login Page</h1><br></br>      
                    <input type="text" placeholder="Username" name="username" onChange={this.onChange} /><br></br>    
                    <input type="password" placeholder="Password" name="password" onChange={this.onChange} /><br></br>    
                    <input type="submit" value="Login" onClick={this.login} /><br></br> 
                    <Link to="/signup">Signup</Link>   

                </div>                
            </div>
        )
    }
}

export default Login;