const Postdata= ()=>{
        const [name, setName] = useState("");
        const [email, setEmail] = useState("");
        const [phone, setPhone] = useState("");

        function saveuser(){
            console.warn({name, email, phone});
            let data = {name, email, phone}
            fetch("http://localhost/projects/php/restapi/api1/post.php",{
                method:"POST",
                headers:{
                    'Accept':'application/json',
                    'Content-type':'application/json'
                },
                body: JSON.stringify(data)
            }).then((result)=>{
                //console.warn("Result",result);
                result.json().then((resp)=>{
                    console.warn("resp",resp);
                    //userlist();
                })
            })
        }
   
        return (
        <div className="App">
            
            <input type="text" value={name} onChange={(e)=>{setName(e.target.value)}} name="name" placeholder="Name" /> <br/><br/>
            <input type="email" value={email} onChange={(e)=>{setEmail(e.target.value)}} name="email" placeholder="Email" /> <br/><br/>
            <input type="phone" value={phone} onChange={(e)=>{setPhone(e.target.value)}} name="phone" placeholder="Phone" /> <br/><br/>
            <button type="button" onClick={saveuser}>Add User</button> <br/><br/>
        </div>
        );
   //}
}  
export default Postdata;