import React, { Component } from 'react'
import {Redirect} from 'react-router-dom';


export default class Home extends Component {

    constructor(props){
        super(props);
        this.state = {
            redirect: false,
            authUser:null
        }
        this.logout = this.logout.bind(this)
    }

    componentWillMount(){
        const userdataname = sessionStorage.getItem("userdataname");
        //const userdataname = localStorage.getItem("userdataname");
        if(sessionStorage.getItem("userdata")){
            console.log("call User Feed");
            console.log(userdataname);
        }else{
            this.setState({redirect: true});
        }
    }

    logout(){
        sessionStorage.setItem("userdata", '');
        sessionStorage.setItem("userdataname", '');
        sessionStorage.clear();
        this.setState({redirect: true});

        /* setUser({});
        setUsername("");
        setPassword(""); */
        localStorage.clear();
    }

    render() {

        if(this.state.redirect){
            return (<Redirect to={'/login'} />)
        }
        const { authUser } = this.state;
        return (
            <div>
                <h1>Home Page</h1>
                <button type="submit" onClick={this.logout}>Logout</button>
                <p>Authenticated User : {authUser}</p> {sessionStorage.getItem("userdataname")}
            </div>
        )
    }
}