import React, { Component } from 'react';
import {Redirect,Link} from 'react-router-dom';
import {Pocessdata} from './Pocessdata';

export default class Signup extends Component {

    constructor(props){
        super(props);
        this.state = {
            name:'',
            email:'',
            phone:'',
            username:'',
            password:'',
            image:'',
            redirect: false
        }
        this.signup = this.signup.bind(this);
        this.onChange = this.onChange.bind(this);
    }

    signup(){
        if(this.state.username && this.state.password && this.state.email){
            //console.warn("Login Function");
            Pocessdata('register.php', this.state).then((result) =>{
                let responseJson = result;
                //console.log(responseJson);
                if(responseJson.userdata){
                    //console.log("ww");
                    sessionStorage.setItem("userdata", responseJson);
                    //console.log("Home Page");
                    this.setState({redirect: true});
                }else{
                    if(responseJson.error){
                        console.log("error");                        
                        console.warn(responseJson);
                    }
                }
            })
        }else{
            console.warn("username && password Require");
        }         
    }

    onChange(e){
        this.setState({[e.target.name] : e.target.value});        
    }


    render() {
        if(this.state.redirect){
            return (<Redirect to={'/'} />)
        }

        if(sessionStorage.getItem("userdata")){
            return (<Redirect to={'/home'} />)
        }
        return (
            <div className="row small-up-2 medium-up-3 large-up-4">
                <div className="column bodyPart">
                    
                    <h1>This is SignUp Page</h1><br></br>     
                    <input type="text" placeholder="name" name="name" onChange={this.onChange} /><br></br>    
                    <input type="email" placeholder="email" name="email" onChange={this.onChange} /><br></br>    
                    <input type="number" placeholder="phone" name="phone" onChange={this.onChange} /><br></br>    
                    <input type="text" placeholder="Username" name="username" onChange={this.onChange} /><br></br>    
                    <input type="password" placeholder="Password" name="password" onChange={this.onChange} /><br></br>
                    <input type="file" placeholder="image" name="image" onChange={this.onChange} /><br></br>     
                    <input type="submit" value="Signup" onClick={this.signup} /><br></br>  
                    <Link to="/login">Login</Link>

                </div>                
            </div>
        )
    }
}
