//import React from 'react';
import React, { useEffect, useState } from 'react';
import 'tachyons';
import {Link} from 'react-router-dom';

//const ThemeContext = React.createContext('light');


//class Getdata extends React.Component{
//render(){
const Data = () => { //Array ES6 Dis-structuring method
    const [item, setItem] = useState([])

    //Update
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [phone, setPhone] = useState("");
    const [id, setId] = useState(null);


    useEffect(() => {
        userlist();
    }, [])

    function userlist() {
        fetch("http://localhost/projects/php/restapi/api1/api.php")
            .then((result) => {
                result.json()
                    .then((resp) => {
                        //console.warn("result", resp)
                        setItem(resp);

                        setName(resp[0].name);
                        setEmail(resp[0].email);
                        setPhone(resp[0].phone);
                        setId(resp[0].id);

                    })
            })
    }
    //console.warn("result", item); 

    function dataUser(id) {
        //alert(id);
        fetch("http://localhost/projects/php/restapi/api1/delete.php/" + id, {
            method: 'DELETE'
        }).then((result) => {
            result.json().then((resp) => {
                console.warn(resp);
                userlist();

            })
        })

    }

    function useredit(id) {
        //console.warn(item[id]);

       // console.warn(id - 1);

        let itemsets = item[id - 1];

        setName(itemsets.name);
        setEmail(itemsets.email);
        setPhone(itemsets.phone);
        setId(itemsets.id);        
    }


    function updateUser(){
       // alert(id);      

        console.warn([name, email, phone , id]);
        let data = {name, email, phone , id}

        fetch("http://localhost/projects/php/restapi/api1/update.php/" + id, {
            method:"PUT",
            headers:{
                'Accept':'application/json',
                'Content-type':'application/json'
            },
            body:JSON.stringify(data)
        }).then((result) => {
            userlist();
             //console.warn("Result",result);
             /* result.json().then((resp)=>{
                console.warn("resp",resp);
                userlist();
            }) */
           
        })        
    }

    return (
        <div className="App">
            <table border="1">
                <tr>
                    <td>Id</td>
                    <td>name</td>
                    <td>title</td>
                    <td>body</td>
                    <td>Action</td>
                </tr>
                {
                    item.map((items) =>
                        <tr>
                            <td>{items.id}</td>
                            <td>{items.name}</td>
                            <td>{items.email}</td>
                            <td>{items.phone}</td>
                            <td>
                                <button onClick={() => dataUser(items.id)}>Delete</button>|
                                <button onClick={() => useredit(items.id)}>Edit</button>|
                                <Link to={'update/'+items.id}>Update</Link>
                            </td>
                        </tr>
                    )
                }
            </table>
            <div>
                <input type="text" value={name} onChange={(e)=>{setName(e.target.value)}} name="name" placeholder="Name" /> <br /><br />
                <input type="email" value={email} onChange={(e)=>{setEmail(e.target.value)}} name="email" placeholder="Email" /> <br /><br />
                <input type="phone" value={phone} onChange={(e)=>{setPhone(e.target.value)}} name="phone" placeholder="Phone" /> <br /><br />
                <button type="button" onClick={updateUser}>Update User</button>
            </div>
        </div>
    );
    //}
}
export default Data;