import React, { Component } from 'react'
import '../App.css';
export default class PreviewImage extends Component {
    state = {
        profileImg: 'https://image.shutterstock.com/image-vector/default-avatar-profile-icon-grey-260nw-518740741.jpg'
    }
    imageHandler = (e)=>{
        const reader =new FileReader();
        reader.onload =()=>{
            this.setState({profileImg: reader.result})
        }
        reader.readAsDataURL(e.target.files[0])
    }
    render() {
        const {profileImg} = this.state;
        return (
            <div>
                <h1>Add Image</h1>
                <img src={profileImg} alt="" id="img" className="preview" />
                <input type="file" name="image-upload" id="input" accept="image/*" onChange={this.imageHandler}/>
            </div>
        )
    }
}
